.burger {
    display: inline-flex;
    align-items: center;
    position: fixed;
    top: 40px;
    right: -15px;
    height: 60px;
    width: 80px;
    padding-left: 6px;
    background: linear-gradient(90deg, #414141 15%, #343434 91.4%);
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    cursor: pointer;
    z-index: 10;
    transition: all 1000ms ease-in-out;
}

.burgerCircle {
    height: 50px;
    width: 50px;
    border: 6px solid #958f78;
    background-color: #fff;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.burgerActive {
    animation: burgerHide 500ms ease-in-out;
    right: -100px;
}

.menu {
    position: fixed;
    top: -210px;
    z-index: 10;
    background: linear-gradient(1deg, transparent 15%, #343434 90%);
    color: #fff;
    height: 140px;
    width: 100%;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    transition: all 1500ms ease;
}

.menuList {
    width: 100%;
    display: flex;
    justify-content: space-around;
    margin: 0;
    padding: 0 50px 0 0;
}

.menuItem {
    cursor: pointer;
    color: #aeaeae;
    margin-right: 15px;
    position: relative;
    transition: all 300ms;
    padding: 30px;
}

.menuItem:after {
    content: '';
    position: absolute;
    bottom: 18px;
    background-color: var(--title-line-color);
    height: 1px;
    width: 40%;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    align-self: flex-end;
    opacity: 0;
    transition: all 700ms;
}

.menuItem:hover.menuItem:after {
    opacity: 1;
}

.menuItem:hover {
    color: #fff;
}

.menuActive {
    top: 0;
}

.close {
    cursor: pointer;
    padding: 20px;
    box-sizing: content-box;
}

@keyframes burgerHide {
    from {
        right: -10px;
    }
    50% {
        right: 0;
    }
    to {
        right: -100px;
    }
}
@keyframes menuShow {
    from {
        right: -500px;
    }
    50% {
        right: 0;
    }
    to {
        right: -10px;
    }
}


@media (max-width: 900px) {
    .menu {
        top: -200vh;
        right: 0;
        height: 100vh;
        width: 100%;
        background: linear-gradient(6deg, transparent 25%, #343434 34%);
    }

    .menuActive {
        top: 0;
    }

    .menuList {
        position: absolute;
        top: 80px;
        left: 40px;
        flex-direction: column;
    }

    .menuItem:after {
        bottom: 47%;
    }

    .close {
        top: 20px;
        right: 20px;
        position: absolute;
    }
}

@media (max-width: 500px) {

    .menuList {
        left: 20px;
    }

    .menuItem:after {
        transform: translateX(0);
    }

}
