.wrapper {
    max-width: 400px;
    margin-bottom: 70px;
}

.slide {
    width: 100%;
}

.description {
    padding: 20px;
    line-height: 24px;
}

.title {
    font-size: 18px;
    margin-bottom: 15px;
}

.text {
    margin: 0;
    word-break: break-word;
}
.slider {
    position: relative;
}

.slider:before {
    content: '';
    display: block;
    background-image: url("../../img/footer/github.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 100% 50%;
    position: absolute;
    background-color: var(--section-bg-color-dark);
    z-index: 10;
    opacity: 0;
    width: 100%;
    height: 100%;
    transition: all 300ms ease;
}

.slider:hover:before {
    opacity: .7;
}

@media (max-width: 530px) {
    .wrapper {
        width: 100%;
    }
}
