@import-normalize;

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'NunitoSans', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@font-face {
    font-family: 'NunitoSans';
    src: url('fonts/NunitoSans-Regular.eot');
    src: url('fonts/NunitoSans-Regular.woff2') format('woff2'), url('fonts/NunitoSans-Regular.woff') format('woff'), url('fonts/NunitoSans-Regular.ttf') format('truetype'), url('fonts/NunitoSans-Regular.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NunitoSans';
    src: url('fonts/NunitoSans-Bold.eot');
    src: url('fonts/NunitoSans-Bold.woff2') format('woff2'), url('fonts/NunitoSans-Bold.woff') format('woff'), url('fonts/NunitoSans-Bold.ttf') format('truetype'), url('fonts/NunitoSans-Bold.svg') format('svg');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'NunitoSans';
    src: url('fonts/NunitoSans-Black.eot');
    src: url('fonts/NunitoSans-Black.woff2') format('woff2'), url('fonts/NunitoSans-Black.woff') format('woff'), url('fonts/NunitoSans-Black.ttf') format('truetype'), url('fonts/NunitoSans-Black.svg') format('svg');
    font-weight: 900;
    font-style: normal;
}
