.projects {
    min-height: 100vh;
    background-color: var(--section-bg-color-dark);
    padding-top: 35px;
    position: relative;
}

.projectsWrapper {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    width: 100%;
    gap: 20px;
    margin-top: 30px;
}

.projectsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 90px;
}


@media (max-width: 1042px) {
    .projectsWrapper {
        justify-content: center;
    }
}


