.title {
    margin: 90px auto;
    color: #f3f3f3;
    position: relative;
    z-index: 1;
}

.title:before {
    content: "";
    display: block;
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 2px;
    border-radius: 2px;
    background-color: #fee604;
    z-index: -1;
}
