.skills {
    min-height: 100vh;
    background-color: var(--section-bg-color-dark);
    padding-top: 35px;
}

.skillsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.skillsWrapper {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    gap: 20px;
}

@media (max-width: 792px) {
    .skillsWrapper {
        flex-direction: column;
        align-items: center;
    }
}
